@import url(https://fonts.googleapis.com/css?family=Audiowide&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box; }

h1, h2, h3, h4, h5, h6 {
  color: #272E46;
  font-family: "Open Sans", sans-serif;
  margin: 20px 0;
  font-weight: 700; }

h1 {
  font-size: 1.5em; }

h2 {
  font-size: 1.25em; }

h3 {
  font-size: 1em; }

h4 {
  font-size: .75em; }

p {
  color: #BBE6F5;
  font-family: "Open Sans", sans-serif;
  margin: .05em 0; }

.component-container {
  width: 100%;
  max-width: 600px;
  text-align: center;
  margin: 10px auto;
  padding: 20px; }

form {
  font: "Open Sans", sans-serif;
  margin: 10px auto 0 auto;
  padding: 16px; }

form input[type="text"],
form input[type="date"],
form input[type="datetime"],
form input[type="email"],
form input[type="number"],
form input[type="search"],
form input[type="time"],
form input[type="url"],
form textarea,
form select {
  outline: none;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  border-radius: 3px;
  background: #fff;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  padding: 10px;
  color: #555;
  font: "Open Sans", sans-serif; }

form input[type="text"]:focus,
form input[type="date"]:focus,
form input[type="datetime"]:focus,
form input[type="email"]:focus,
form input[type="number"]:focus,
form input[type="search"]:focus,
form input[type="time"]:focus,
form input[type="url"]:focus,
form textarea:focus,
form select:focus {
  box-shadow: 0 0 5px #0f3966;
  padding: 10px;
  border: 1px solid #0f3966; }

form input[type="submit"],
form input[type="button"], button {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  padding: 10px;
  background: #008250;
  border-bottom: 2px solid #0f3966;
  border-top-style: none;
  border-right-style: none;
  border-left-style: none;
  color: #fff;
  border-radius: 3px;
  clear: both; }

form input[type="submit"]:hover,
form input[type="button"]:hover, button:hover {
  background: #2EBC99; }

select {
  -webkit-appearance: menulist-button;
  height: 35px; }

#Review .movie-info {
  display: flex; }
  #Review .movie-info img {
    width: 150px; }
  #Review .movie-info .movie-poster {
    margin-right: 1.5em; }
  #Review .movie-info .synopsis {
    text-align: left;
    font-size: .8em; }
  #Review .movie-info h1, #Review .movie-info h2, #Review .movie-info h3, #Review .movie-info h4 {
    text-align: left; }
  #Review .movie-info h1 {
    margin-top: 0; }
  #Review .movie-info h4 {
    margin-bottom: 0; }

#login {
  margin-top: 4em;
  text-align: center; }
  #login body {
    color: #BBE6F5;
    background-color: #48798A;
    font-family: "Open Sans", sans-serif; }
  #login h1 {
    margin-bottom: 20px;
    font-size: 30px; }
  #login button, #login form input[type="text"] {
    width: 100%; }
  #login form {
    max-width: 400px; }

#dashboard {
  color: #BBE6F5;
  padding-bottom: 70px; }
  #dashboard #react-tab {
    width: 100%;
    margin: auto; }
  #dashboard #react-tabtab-panel-0, #dashboard #react-tabtab-panel-1 {
    height: 300px; }
  #dashboard form {
    position: fixed;
    bottom: 0;
    left: 0;
    text-align: center;
    background-color: #3A3963;
    padding: 10px;
    width: 100%; }
  #dashboard form input[type="text"] {
    width: 75%;
    max-width: 370px;
    margin-right: 10px;
    margin-bottom: 0; }
  #dashboard button {
    width: 20%;
    max-width: 80px; }
  #dashboard .profile-pic {
    width: 120px;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 60px; }
  #dashboard h2 {
    margin: 0;
    font-weight: 700;
    font-size: 1em; }

#AboutUs img {
  width: 150px;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 75px; }

#AboutUs hr {
  margin: 50px 150px;
  height: 1px;
  border: none;
  color: #e4cc46;
  background-color: #e4cc46; }

nav {
  color: #BBE6F5;
  font-family: "Open Sans", sans-serif;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: #2F6A84; }

nav > link, nav > a {
  color: #272E46;
  margin: .5em;
  text-decoration: none; }

nav link:visited, nav a:visited {
  color: #272E46; }

nav > ul {
  width: 50%;
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
  flex-direction: row; }

nav > ul > a {
  margin-left: .5em;
  margin-right: .5em;
  text-decoration: none; }

#navbar-links li {
  color: #272E46;
  font-weight: bold; }

#title-logo {
  position: relative;
  width: 7.5em;
  height: 6.25em;
  margin: .2em;
  background: #0E2E3E;
  border-radius: 50% / 10%;
  color: #272E46;
  line-height: 5em;
  text-align: center;
  text-indent: .1em;
  text-shadow: -2px -2px 4px #00F7FF, 2px 2px 4px #00F7FF; }

#title-logo:before {
  font-size: 2em;
  font-family: "Audiowide", cursive;
  content: 'VEWR';
  position: absolute;
  top: 10%;
  bottom: 10%;
  right: -5%;
  left: -5%;
  background: inherit;
  border-radius: 5% / 50%; }

#Search .movie-list img {
  width: 80px; }

#Search .movie-list .synopsis {
  font-size: 0.8em;
  float: left;
  text-align: left; }

#Search .movie-list li {
  display: flex;
  margin-bottom: 1.5em; }

#Search .movie-list .movie-poster {
  margin-right: 1em; }

#Search .movie-list h3 {
  margin-top: 0;
  margin-bottom: 0;
  text-align: left; }

body {
  color: #BBE6F5;
  background-color: #48798A;
  font-family: "Open Sans", sans-serif;
  line-height: 1.25; }

