* {box-sizing: border-box;}

h1, h2, h3, h4, h5, h6 {
  color: $nav-text-theme-1; 
  font-family: $heading-font-stack;
  margin: 20px 0;
  font-weight: 700;
}

h1 {font-size: 1.5em}
h2 {font-size: 1.25em}
h3 {font-size: 1em}
h4 {font-size: .75em}

p {
  color: $text-theme-1; 
  font-family: $body-font-stack;
  margin: .05em 0;
}


